@import "../../styles/utils.scss";

.control {
  &__input-wrapper {
    @extend %flex-row-center;

    padding: 11px 16px;

    border: 1px solid $color-gray;
    transition: border-color 300ms;
  }

  &--focused {
    border-color: darken($color-gray, 20%);
  }

  &_icon {
    margin-right: 10px;
  }

  &__input {
    max-height: 24px;
    line-height: 24px;

    &--time {
      min-width: 75px;
    }

    &--small {
      @media (max-width: $break-point-tablet-lg) {
        max-width: 75px;
      }
    }
  }

  &__label {
    font-size: 14px;
    color: #626262;

    margin-bottom: 8px;
  }

  & + & {
    margin-left: 20px;
  }
}

.tablet {
  @media (max-width: 1000px) {
    width: 200px;
  }

  @media (max-width: 860px) {
    width: 150px;
  }
}

.dropdown {
  position: relative;
  cursor: pointer;

  &:hover {
      transition-delay: 3s;
      .dropdown-leagues { 
          display: block;
      }
  }
  >img {
      max-width: 25px;
  }
  .dropdown-leagues {
      width: 100%;
      display: none;
      position: absolute;
      top: 100%;
      left: 0;

      margin: 0;
      //border: 1px solid $color-gray;
        
      background-color: #fff;
      z-index: 99;

      transition-delay: 3s;
  }
}

.dropdown-leagues li:nth-child(2n) {
  border-top: none;
}

.league {
  border: 1px solid $color-gray;
  padding: 11px 16px;
  transition: background-color 300ms;

  &:hover {
    background-color: lighten($color-gray, 5);
  }

  .black {
    color: #000;
  }
}

.league_even {
  border-top: none !important;
}