@import './utils.scss';

*,
::after,
::before {
  box-sizing: border-box;
}

body,
button,
a,
select,
input {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

body {
  &::-webkit-scrollbar { 
    width: 5px; 
    height: 3px;
  }

  &::-webkit-scrollbar-track-piece { 
    background-color: #fff;
  }

  &::-webkit-scrollbar-thumb { 
    height: 50px; 
    background-color: #666; 
    border-radius: 3px;
  }
}

input {
  border: none;
  background: transparent;
  outline: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
  background: none;
  outline: none;
}

.content {
  position: relative;

  max-width: 1160px;
  padding: 0 34px;
  margin: 0 auto;

  &-indent {
    padding-top: 40px;
  }

  @media (max-width: $break-point-phone) {
    padding: 0 20px;
  }

  @media (min-width: $break-point-large) {
    max-width: 1520px;
  }
}
